import React from 'react';
import ReactDOM from 'react-dom';
import App from './App';
import './index.css';

import { createStore, applyMiddleware, compose } from 'redux'
//import ChatApp from './reducers'

// DEVELOPMENT ONLY
import { createLogger } from 'redux-logger'
import ChatApp from './reducers'
import { Provider } from 'react-redux'

// import createSocketIoMiddleware from 'redux-socket.io'
// import io from 'socket.io-client'
// import { Socket } from 'phoenix'
import channelsMiddleware from './middleware'

// import injectTapEventPlugin from 'react-tap-event-plugin'

// // Needed for onTouchTap
// // http://stackoverflow.com/a/34015469/988941
// injectTapEventPlugin()

var loggerMiddleware
var store

if (process.env.NODE_ENV === 'development') {
	loggerMiddleware = createLogger()
	store = createStore(
		ChatApp, 
		compose(
			applyMiddleware(
				channelsMiddleware('ws://localhost:4000/socket', 'http://localhost:4000/room'),
				loggerMiddleware
			),
			// window.__REDUX_DEVTOOLS_EXTENSION__ && window.__REDUX_DEVTOOLS_EXTENSION__()
	))
}

if (process.env.NODE_ENV === 'production') {
	store = createStore(
		ChatApp, 
		compose(
			applyMiddleware(
				channelsMiddleware('wss://atm-chat.herokuapp.com/socket', 'https://atm-chat.herokuapp.com/room')
			)))
}

ReactDOM.render(
	<Provider store={store}>
 		<App />
	  </Provider>,
  document.getElementById('root')
);



